<template>
  <div class="blog-add">
    <div class="title-nav">
      <button @click.stop="$router.go(-1)" class="back">返回</button>
      <input v-model="blog.title" type="text" placeholder="请输入标题!" />
      <button @click.stop="handleSave" v-show="type === 'add'" class="save">保存草稿</button>
      <button
        @click.stop="dialogFormVisible = true"
        class="public"
      >{{ type === "add" ? "发布文章" : "保存修改" }}</button>
    </div>
    <p class="img-tips" v-show="showTips">
      图片的大小可以通过query参数w,h指定大小(imgURL?w=100&h=100)
      <span
        @click.stop="hideTips"
        class="iconfont icon-xianshi_quxiaotianchong"
      ></span>
    </p>
    <div class="edit">
      <mavon-editor
        @imgAdd="imgAdd"
        @imgDel="imgDel"
        @change="handleChange"
        :imageFilter="imageFilter"
        v-model="value"
        codeStyle="atelier-plateau-light"
        class="edit-main"
        ref="addBlogInput"
      />
    </div>
    <el-dialog title="发布文章" :visible.sync="dialogFormVisible">
      <p>
        添加标签&emsp;:
        <el-tag
          :key="tag"
          v-for="tag in blog.articleTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)"
        >{{ tag }}</el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        ></el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+&emsp;添加标签</el-button>
      </p>
      <p>
        分类专栏&emsp;:
        <el-select
          v-model="blog.types"
          multiple
          filterable
          allow-create
          default-first-option
          placeholder="请选择分类"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </p>

      <p>
        发布形式&emsp;:
        <el-checkbox v-model="blog.privated">私有</el-checkbox>
      </p>
      <p class="control">
        <button @click.stop="dialogFormVisible = false" class="save cancel">取消</button>
        <button @click.stop="handlePublic" class="save comfirm">确认</button>
      </p>
    </el-dialog>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  components: {
    "mavon-editor": mavonEditor
  },
  data() {
    return {
      blog: {
        privated: false,
        title: "",
        markdown: "",
        types: [],
        articleTags: ["js"],
        html: "",
        _id: undefined
      },
      tags: [] /*包含了原有图片+上传图片 的file_id与路径*/,
      value: "",
      type: "add",

      dialogFormVisible: false,
      inputVisible: false,
      inputValue: "",

      //远程分类列表
      remoteTypes: [],
      showTips: true
    };
  },
  computed: {
    options() {
      return this.remoteTypes.map(v => {
        return { value: v.name, label: v.name };
      });
    }
  },
  created() {
    let { id, type } = this.$route.query;
    if (id && type === "edit") {
      this.type = "edit";
      this.getBlogById(id);
    } else {
      this.value = "";
      this.$message({
        message: "当前为发布模式!",
        showClose: true,
        type: "success"
      });
    }
    //获取分类标记
    this.getBlogTypes();
  },
  methods: {
    hideTips() {
      this.showTips = false;
    },
    async getBlogTypes() {
      let remoteTypes = await this.$store.dispatch("getBlogTypes");
      this.remoteTypes = remoteTypes || [];
    },
    handleChange(markdown, html) {
      this.blog.markdown = markdown;
      this.blog.html = html;
    },
    handleSave() {
      console.log(this.blog);
    },
    async handlePublic() {
      console.log(this.blog);
      let data = null;
      if (this.type === "add") {
        data = await this.$store.dispatch("AddBlog", {
          context: this.blog,
          tags: this.tags
        });
      } else if (this.type === "edit") {
        data = await this.$store.dispatch("editBlog", {
          context: this.blog,
          tags: this.tags
        });
      }

      if (data) {
        this.$message({
          message: "发布成功",
          showClose: true,
          type: "success"
        });
        this.$router.replace("/show-blog/" + data._id);
      }
    },
    async getBlogById(id) {
      let blog = await this.$store.dispatch("getBlogById", id);
      this.blog = blog;
      this.value = blog.markdown;
      this.tags = blog.thumbnails;
    },
    handleClose(tag) {
      this.blog.articleTags.splice(this.blog.articleTags.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.blog.articleTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    async imgAdd(filename, file) {
      let formdata = new FormData();
      formdata.append("image", file);
      let backData = await this.$store.dispatch("blogAddIMG", formdata);
      this.tags.push(backData);
      this.$refs.addBlogInput.$img2Url(filename, backData.src);
      this.showTips = true;
    },
    /*图片大小过滤等 */
    imageFilter(file) {
      return true;
    },
    imgDel(filename) {
      console.log(filename);
    }
  }
};
</script>

<style lang="scss" scoped>
.blog-add {
  .img-tips {
    color: $uni-color-warning;
    text-align: center;
    padding: 5px 0;
    span {
      color: $uni-color-error;
      cursor: pointer;
    }
  }
  button.save {
    height: 36px;
    width: 98px;
    margin-right: 16px;
    padding: 0 16px;
    white-space: nowrap;
    color: $uni-color-error;
    border: 1px solid $uni-color-error;
    outline: none;
    @extend .hover-nav;
  }
  .control {
    text-align: end;
    button {
      border-radius: $uni-border-radius-base;
      color: #fff;
      cursor: pointer;
      border: 0;
    }
    .cancel {
      background-color: $uni-color-warning;
    }
    .comfirm {
      background-color: $uni-color-primary;
    }
  }

  button.public {
    margin-right: 16px;
    padding: 0 16px;
    height: 36px;
    width: 98px;
    background-color: $uni-color-error;
    color: #fff;
    outline: none;
    border: 0;
    @extend .hover-nav;
  }
  .title-nav {
    height: 55px;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    padding: 0 24px;
    button.back {
      outline: none;
      font-size: 1rem;
      border: 0;
      background-color: #f3f4f5;
      padding: 0.5rem;
      @extend .hover-nav;
    }
    button {
      white-space: nowrap;
      cursor: pointer;
      border-radius: $uni-border-radius-sm;
    }
    input {
      height: 36px;
      width: 100%;
      margin: 0 16px;
      padding: 0 0.5rem;
    }

    .el-checkbox {
      margin: 0 16px;
    }
  }
  .el-dialog__body {
    p {
      margin-bottom: 1rem;
      * {
        margin-left: 5px;
      }
    }
  }
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: middle;
  }
  .edit {
    height: 100%;
    .edit-main {
      height: calc(100vh - 55px - 40px);
    }
  }
  .el-select {
    width: 50%;
  }
}
</style>